}
.modal-dialog,
.btn-primary,
.controls button {
  color: hsl(0, 0%, 100%);
}
.bts-toggle-mode {
  cursor: pointer;
  position: absolute;
  top: 20px;
  left: 20px;
  background-color: hsl(231, 15%, 18%);
  border: 1px solid hsl(0, 0%, 100%);
  border-radius: 2px !important;
  padding: 6px;
  font-size: 16px;
  color: hsl(0, 0%, 100%);
  user-select: none;
  display: inline-flex;
}
.djs-container .djs-minimap {
  background-color: rgba(40, 42, 54, 0.9);
}
.bio-properties-panel-description,
.bio-properties-panel-description p,
.bio-properties-panel-description span,
.bio-properties-panel-description div {
  color: var(--dracula-purple);
}
.djs-popup .entry:hover {
  color: var(--dracula-purple);
}
.djs-context-pad {
  position: absolute;
  display: none;
  pointer-events: none;
  line-height: 1;
  background-color: var(--dracula-background);
  border: 1px solid var(--dracula-comment);
  padding: 5px;
  width: 42px !important;
}
.djs-context-pad .group:not(:last-child) {
  border-bottom: 1px solid var(--dracula-comment);
}
.djs-context-pad .entry {
  border-radius: 0px !important;
  padding: 0px;
  background-color: var(--context-pad-entry-background-color);
  box-shadow: 0 0 0px 0px var(--context-pad-entry-background-color) !important;
  pointer-events: all;
  vertical-align: middle;
  width: 30px;
  height: 22px;
  margin: 5px 0 5px 0;
}
.djs-context-pad .entry:hover {
  background: var(--dracula-background);
  color: var(--dracula-purple);
}
.djs-context-pad.open {
  display: block;
}
.djs-element.selected .djs-outline,
.djs-selection-outline {
  stroke-dasharray: 2;
  stroke-dashoffset: 500;
  animation: dash 80s linear infinite;
  stroke: var(--element-selected-outline-stroke-color);
}
.layer-selectionOutline .djs-selection-outline {
  stroke: none;
}
@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}
.tabs .primary {
  background-color: var(--dracula-background) !important;
  color: var(--dracula-foreground) !important;
}
.tabs .active {
  background-color: var(--dracula-current-line) !important;
  color: var(--dracula-foreground) !important;
}
